    let navParent = document.querySelector(".navigation")

    for (let i = 0; i < navParent.childElementCount; i++) {
      navParent.children[i].addEventListener('click', () => {
        window.scrollTo(0, parseInt(navParent.children[i].id));
      })
    }
    window.addEventListener('scroll', () => {
      let scrollValue = window.scrollY;
      console.log(scrollValue)
      if (scrollValue < 200 ) {
        for (let i = 0; i < navParent.childElementCount; i++) {
          navParent.children[i].classList = ''
        }
        navParent.children[0].classList = "nav-active"
      } else if (scrollValue < 800) {
        for (let i = 0; i < navParent.childElementCount; i++) {
          navParent.children[i].classList = ''
        }
        navParent.children[1].classList = "nav-active"

      } else if (scrollValue < 1170) {
        for (let i = 0; i < navParent.childElementCount; i++) {
          navParent.children[i].classList = ''
        }
        navParent.children[2].classList = "nav-active"

      }
    })
  }
}
</script>
